import react from "react";
import React, { Component } from "react";
import { Fragment } from "react/cjs/react.production.min";
import { createStore } from "redux";
import { CommentList, CreateComment } from "../Component/comment";
import { addToCart, ProductService } from "../Component/product";
import { cartstore } from "../Component/product";
export class Detail extends Component {
  state = {
    data: [],
  };

  componentDidMount() {
    const id = this.props.match.params.id;
    ProductService.getProuductById(id).then(({ data }) =>
      this.setState({ data })
    );

    this.unsubscribe = cartstore.subscribe(() => {
      console.log(cartstore.getState());
    });
    // const off = this.props.match.params.id;
    // ProductService.getProuductById("off").then(({ data }) =>
    //   this.setState({ data })
    // )
  }
  componentWillUnmount() {
    this.unsubscribe();
  }
  async submitComment(comment) {
    const response = await ProductService.addComment(
      this.state.data.id,
      comment
    );
    console.log("inja1: ", response);
    if (response.status === 200) {
      console.log("inja2");
      const { data } = await ProductService.getComments(this.state.data.id);
      console.log(data);
      this.setState({ data: { ...this.state.data, comments: data } });
    }
  }
  addToCartHandler() {
    cartstore.dispatch(addToCart(this.state.data));
  }

  render() {
    const data = this.state.data;
    if (!data) {
      return <div>loading...</div>;
    }
   

    return (
      <div className="container" style={{            backgroundColor: "#eeeeee",
    }}>
        <div className="row" style={{direction:"ltr"}}>
          <div className="col-5 col-lg-5 col-md-7  ">
            <img
              alt={data.title}
              src={data.pic}
              style={{ width: "400px", height: "400px",  boxShadow:"4px 4px 10px 0 rgba(213, 212, 239, 0.7)"
            }}
            />
          </div>
          <div
            id="picdetail"
            className="col-3  col-lg-3 offset-lg-10"
            style={{
              direction: "rtl",
              border: "1px",
              textIndent: "3px",
            }}
          >
            <p id="title">{data.title}</p>
            <br />
            <br />
            <br />
            <br />
            <ul>
              ویژگی های کفش :<li>نحوه بستن کفش :بندی </li>
              <li> :قابلیت ارتجاعی ،طبی</li>
              <li>زیره :کاهش فشار وارده</li>
            </ul>
        

          </div>
          <div
            className="col-3  col-lg-3"
            style={{
              fontFamily: "sans-serif",
              marginTop: "2px",
              textAlign: "right",
            backgroundColor: "#eee",
            margin:"10px",
            marginBottom:"auto",
            borderRadius:"10px",
            width:"270px",
            boxShadow:"4px 4px 10px 0 rgba(213, 212, 239, 0.7)",
            // height:"400px"
            // margin-bottom: auto;
            // background-color: #eeeeee;
            height:"400px",
            // border-radius: 10px;
            // margin: 10px;

            }}
          >
            <br/>
            <span style={{ color: "green", fontWeight: "bold" }}>
              موجود در انبار
              <img
                width="30px"
                src="https://www.clipartmax.com/png/middle/291-2916483_shop-icon-store-icon-white-png.png"
              />
            </span>
            <hr />
            <span style={{ color: "green", fontWeight: "bold" }}>
              ارسال رایگان{" "}
              <img
                width="40px"
                src="data:image/jpeg;base64,"
              />
            </span>
            <hr />
            <div
              style={{
                height: "20px",
                width: "70px",
                display: "flex",
                fontFamily: "sans-serif",
              }}
            >
              {/* <del><b>{parseInt(data.priceOff).toLocaleString()}</b> </del> */}
            </div>
<br/>
<br/>
              <br/>
              <br/>
            <div
              style={{
                color: "#000",
                display: "flex",
                textIndent: "8px",
                fontFamily: "sans-serif",
              }}
            >
              
              <p> تومان </p>
              <span>
                <b>{parseInt(data.price).toLocaleString()}</b>{" "}
              </span>
           
            </div>
          
          
            <button
              style={{ height: "40px", width:"100%",fontFamily: "sans-serif" }}
              onClick={this.addToCartHandler.bind(this)}
              className=" mt-5 btn btn-danger btn-lg"
            >
              افزودن به سبد خرید
            </button>
          </div>
      
        <hr/>
        </div>
        <div className="col-3  col-lg-3"  style={{}}>
        <div >
             <span style={{fontFamily:"sans-serif",textAlign:"right"}}  >نظرات کاربران</span>

             <CreateComment onComment={this.submitComment.bind(this)} />
             <CommentList comments={data.comments || []} />
            
           
           </div>
           </div>
    </div>
    );
  }
}
